<script>
import {mapActions, mapState} from "vuex";
import {getCheckoutOrderListApi, submitOrderListApi} from "@/api/checkout";

export default {
  name: "Checkout",
  async created() {
    // 请求默认地址id
    await this.getDefaultAddressId()

    // 请求地址列表
    await this.getAddressList()

    // 请求订单列表
    await this.getOrderList()
  },
  data() {
    return {
      order: {}, // 后端拿到的订单数据
      personal: {}, // 后端拿到的个人数据
      remark: '' // 买家留言
    }
  },
  computed: {
    mode() {
      return this.$route.query.mode
    },
    cartIds() {
      return this.$route.query.cartIds
    },
    goodsObj() {
      return {
        goodsId: this.$route.query.goodsId,
        goodsSkuId: this.$route.query.goodsSkuId,
        goodsNum: this.$route.query.goodsNum,
      }
    },

    // 默认地址id
    ...mapState('address', ['defaultAddressId', 'addressList']),

    // 被选中的地址，判断有无默认id，无默认id就给地址列表第一项，有默认id就给默认地址，如果地址列表都没有，那就是给undefined
    selectedAddress() {
      if (!this.defaultAddressId) return this.addressList[0]
      else return this.addressList.find(item => item.address_id === this.defaultAddressId)
    },

    // 拼接省市区和详细地址
    info_address() {
      const obj = this.selectedAddress.region
      return obj.province + obj.city + obj.region + this.selectedAddress.detail
    }
  },
  methods: {
    // 请求 默认地址id 和 地址列表
    ...mapActions('address', ['getDefaultAddressId', 'getAddressList']),

    // 请求订单列表
    async getOrderList() {
      if (this.mode === 'cart') {
        const {data: {order, personal}} = await getCheckoutOrderListApi(this.mode, {cartIds: this.cartIds})
        this.order = order
        this.personal = personal
      } else if (this.mode === 'buyNow') {
        const {data: {order, personal}} = await getCheckoutOrderListApi(this.mode, this.goodsObj)
        this.order = order
        this.personal = personal
      }
    },

    // 提交订单
    async onsubmitOrderList() {
      if (this.mode === 'cart') {
        await submitOrderListApi(this.mode, {cartIds: this.cartIds, remark: this.remark})
      } else if (this.mode === 'buyNow') {
        await submitOrderListApi(this.mode, {...this.goodsObj, remark: this.remark})
      }
      this.$toast.success('支付成功')
      this.$router.replace('/orders')
    }
  },
}
</script>

<template>
  <div class="orders">
    <van-nav-bar title="订单结算台" left-arrow @click-left="$router.go(-1)"/>

    <!-- 地址相关 -->
    <div @click="$router.push('/address')" class="address">

      <div class="left-icon">
        <van-icon name="logistics"/>
      </div>

      <div v-if="selectedAddress" class="info">
        <div class="info-content">
          <span class="name">{{ selectedAddress.name }}</span>
          <span class="mobile">{{ selectedAddress.phone }}</span>
        </div>
        <div class="info-address">
          {{ info_address }}
        </div>
      </div>

      <div v-else class="info">
        请选择配送地址
      </div>

      <div class="right-icon">
        <van-icon name="arrow"/>
      </div>
    </div>

    <!-- 订单明细 -->
    <div v-if="order.goodsList" class="pay-list">
      <div class="list">
        <div v-for="item in order.goodsList" :key="item.goods_id" class="goods-item">

          <div class="left">
            <img :src="item.goods_image" alt=""/>
          </div>

          <div class="right">
            <p class="tit text-ellipsis-2">
              {{ item.goods_name }}
            </p>
            <p class="info">
              <span class="count">x{{ item.total_num }}</span>
              <span class="price">¥{{ item.goods_price }}</span>
            </p>
          </div>

        </div>
      </div>

      <div class="flow-num-box">
        <span>共 {{ order.orderTotalNum }} 件商品，合计：</span>
        <span class="money">￥{{ order.orderTotalPrice }}</span>
      </div>

      <div class="pay-detail">
        <div class="pay-cell">
          <span>订单总金额：</span>
          <span class="red">￥{{ order.orderTotalPrice }}</span>
        </div>

        <div class="pay-cell">
          <span>优惠券：</span>
          <span>无优惠券可用</span>
        </div>

        <div class="pay-cell">
          <span>配送费用：</span>
          <!--判断有没有配送地址-->
          <span v-if="!selectedAddress">请先选择配送地址</span>
          <span v-else class="red">+￥0.00</span>
        </div>
      </div>

      <!-- 支付方式 -->
      <div class="pay-way">
        <span class="tit">支付方式</span>
        <div class="pay-cell">
          <span><van-icon name="balance-o"/>余额支付（可用 ¥ {{ personal.balance }} 元）</span>
          <!-- <span>请先选择配送地址</span> -->
          <span class="red"><van-icon name="passed"/></span>
        </div>
      </div>

      <!-- 买家留言 -->
      <div class="buytips">
        <textarea v-model="remark" placeholder="选填：买家留言（50字内）"></textarea>
      </div>
    </div>

    <!-- 底部提交 -->
    <div class="footer-fixed">
      <div class="left">实付款：<span>￥{{ order.orderTotalPrice }}</span></div>
      <div @click="onsubmitOrderList" class="tipsbtn">提交订单</div>
    </div>
  </div>
</template>

<style scoped lang="less">
.orders {
  padding-bottom: 46px;

  ::v-deep {
    .van-nav-bar__arrow {
      color: #333;
    }
  }
}

.van-nav-bar {
  position: sticky;
  top: 0;
  width: 100%;
}

.address {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  font-size: 14px;
  color: #666;
  position: relative;
  background: url(@/assets/border-line.png) bottom repeat-x;
  background-size: 60px auto;

  .left-icon {
    margin-right: 20px;
  }

  .right-icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-7px);
  }
}

.goods-item {
  height: 100px;
  margin-bottom: 6px;
  padding: 10px;
  background-color: #fff;
  display: flex;

  .left {
    width: 100px;

    img {
      display: block;
      width: 80px;
      margin: 10px auto;
    }
  }

  .right {
    flex: 1;
    font-size: 14px;
    line-height: 1.3;
    padding: 10px 0 10px 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: #333;

    .info {
      margin-top: 5px;
      display: flex;
      justify-content: space-between;

      .price {
        color: #fa2209;
      }
    }
  }
}

.flow-num-box {
  display: flex;
  justify-content: flex-end;
  padding: 10px 10px;
  font-size: 14px;
  border-bottom: 1px solid #efefef;

  .money {
    color: #fa2209;
  }
}

.pay-cell {
  font-size: 14px;
  padding: 10px 12px;
  color: #333;
  display: flex;
  justify-content: space-between;

  .red {
    color: #fa2209;
  }
}

.pay-detail {
  border-bottom: 1px solid #efefef;
}

.pay-way {
  font-size: 14px;
  padding: 10px 12px;
  border-bottom: 1px solid #efefef;
  color: #333;

  .tit {
    line-height: 30px;
  }

  .pay-cell {
    padding: 10px 0;
  }

  .van-icon {
    font-size: 20px;
    margin-right: 5px;
  }
}

.buytips {

  textarea {
    width: 100%;
    border: none;
    font-size: 14px;
    padding: 12px;
    height: 100px;
  }
}

.footer-fixed {
  position: fixed;
  background-color: #fff;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 46px;
  line-height: 46px;
  border-top: 1px solid #efefef;
  font-size: 14px;
  display: flex;

  .left {
    flex: 1;
    padding-left: 12px;
    color: #666;

    span {
      color: #fa2209;
    }
  }

  .tipsbtn {
    width: 121px;
    background: linear-gradient(90deg, #f9211c, #ff6335);
    color: #fff;
    text-align: center;
  }
}
</style>

